
.p-top-media__items{
  width: 100%;

  @include mq("tab"){
    max-width: rem(1164);
    display: flex;
    align-items: center;
    align-items: flex-start;
    margin: 0 auto;
    padding: 0 $padding-pc;
  }
}

.p-top-media__img{
  width: 100%;
  max-width: rem(557);
  margin: 0 auto;
}

.p-top-media__item{
  width: 100%;
  max-width: rem(510);
  margin: 0 auto;
  padding: 0 $padding-sp;

  @include mq("tab"){
    margin: 0 0 0 rem(32);
    padding: 0;
  }
}

.p-top-media__title{
  font-family: $base-font-family;
  font-size: rem(20);
  font-weight: $regular;
  letter-spacing: 0.1em;
  color: $white;
  text-align: center;
  margin-left: 18px;
  
  @include mq("tab"){
    font-size: rem(25);
    letter-spacing: 0.02em;
    text-align: left;
    margin-left: 0;
    margin-top: rem(27);
  }
}

.p-top-media__title--overview{
  margin-top: rem(24);
}

.p-top-media__text{
  font-family: $base-font-family;
  font-size: rem(16);
  font-weight: $regular;
  line-height: 1.44;
  letter-spacing: 0.12em;
  color: $white;
  margin-top: rem(21);
  
  @include mq("tab"){
    text-align: left;
    margin-top: rem(37);
    line-height: 2.2;
    letter-spacing: 0.03em;
  }

}

.p-top-media__btn{
  text-align: center;
  margin-top: rem(27);

  @include mq("tab"){
    text-align: left;
    margin-top: rem(48);
  }

}